const premium = tariffs.find( el => el.id === 'premium' )


export default function Tariffs() {
   const [collapseGroup, setCollapseGroup] = React.useState( false )
   const [collapseChannels, setCollapseChannels] = React.useState( [] )

   const settingsSlider = {
      dots: true,
      infinite: false,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      responsive: [
         {
            breakpoint: 1280,
            settings: {
               slidesToShow: 3,
            }
         }, {
            breakpoint: 1024,
            settings: {
               slidesToShow: 2,
            }
         }, {
            breakpoint: 768,
            settings: {
               slidesToShow: 1,
               arrows: false,
            }
         }
      ]
   }

   return (
      <section className="tariffs">
         <div className="wrapper">
            <h1 className="tariffs__title"> !</h1>

            <Slider className="slider" {...settingsSlider}>
               {
                  tariffs.map( (tariff, i) => (
                     <TariffCard key={tariff.id}
                                 tariff={tariff}
                                 premium={premium}
                                 collapse={{
                                    collapseGroup,
                                    setCollapseGroup,
                                    collapseChannels,
                                    setCollapseChannels
                                 }}/>
                  ) )
               }


            </Slider>
         </div>
      </section>
   )
}
